<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">项目数据转移</div>
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{name:'administrativeManageHomepageNew'}">行政管理</el-breadcrumb-item>
                <!-- <el-breadcrumb-item :to="{name:'administrativeManageHomepage'}">行政管理</el-breadcrumb-item> -->
                <el-breadcrumb-item>项目数据转移</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="line"></div>
            <div class="datamig_body">
              <div class="datamig_title">项目数据迁移<span>—— 可将以往的项目数据一键式导入本系统</span></div>
              <div class="datamig_sectitle">步骤1 导入模板下载</div>
              <div class="datamig_info">
                <div class="datamig_info_text">导入模板中包含如下两个文件：</div>
                <div class="datamig_info_list">
                  <div class="datamig_info_text"><img src="../../images/datamigration_1.png">项目基本信息导入模板；</div>
                  <div class="datamig_info_text"><img src="../../images/datamigration_2.png">项目文件导入目录，此文件为文件夹目录。
                    <br>(请将以往的文件安装此目录填充，并再压缩成压缩文件并以企业名称全称命)</div>
                </div>
              </div>
              <div class="datamig_btnbox marginb_20">
                <div class="table-group-title-btn btn-blue" @click="downloadClick">立即下载</div>
              </div>
              <div class="datamig_sectitle">步骤2 上传导入</div>
              <uploader :options="options" :fileStatusText="fileStatusText" class="uploader_example">
                <uploader-unsupport></uploader-unsupport>
                <uploader-drop>
                  <p>将文件拖拽到此处或</p>
                  <uploader-btn>选择文件</uploader-btn>
                  <!--                   <uploader-btn :attrs="attrs">select images</uploader-btn>
                  <uploader-btn :directory="true">select folder</uploader-btn> -->
                </uploader-drop>
                <uploader-list></uploader-list>
              </uploader>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { userList, modifyUserStatus, addUserInfo, addUser, getUserMenu, editUserRule,fileTemplate } from '../../config/api'
import api from '../../config/url'; //引用url.js
import { dialogConfirm, dialogConfirmHasbtn, getsignature } from '../../config/mUtils'
export default {
  data() {
    return {
      options: {
        // https://github.com/simple-uploader/Uploader/tree/develop/samples/Node.js
        target: api.url + '/web/File/sliceUpload',
        // target(file,chunk){
        //   console.log(file)
        //   console.log(chunk)
        // },
        //testMethod:'',
        testChunks: true,
        // query: {
        //   signature: getsignature(['chunkNumber', 'chunkSize', 'currentChunkSize', 'totalSize', 'identifier', 'filename', 'relativePath', 'totalChunks'])
        // },
        headers: {
          webToken:window.localStorage.getItem("htcx_webtoken"),
          uid:window.localStorage.getItem("htcx_uid"),
          key:window.localStorage.getItem("htcx_server_key")
        },
        maxChunkRetries: 1,
        chunkSize: 2*1024*1024
      },
      attrs: {
        accept: 'image/*'
      },
      fileStatusText: {
        success: '成功',
        error: '失败',
        uploading: '上传中',
        paused: '暂停',
        waiting: '等待上传'
      }
    }
  },
  created() {
    //this.target=;
    //console.log(this.testMethod);
  },
  computed: {

  },
  mounted() {},
  methods: {
    downloadClick(){
      fileTemplate({
        fileName:'xmzddr'
      }).then(res=>{
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.datamig_body {
  padding: 30px 0px;
}

.datamig_title {
  font-size: 18px;
  color: #1787FB;
  margin-bottom: 25px;
  span {
    font-size: 14px;
    color: #7BB4FC;
  }
}

.datamig_sectitle {
  font-size: 18px;
  color: #35383A;
  font-weight: bold;
  margin-bottom: 16px;
}

.datamig_info_text {
  font-size: 14px;
  color: #808080;
  display: flex;
  margin-bottom: 15px;
  img {
    width: 12px;
    height: 16px;
    margin-top: 3px;
    margin-right: 10px;
  }
}

.datamig_btnbox {
  display: flex;
}

.datamig_info_list {
  margin-left: 20px;
}

.uploader_example {
  width: 600px;
}

</style>



